<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdataModal/chromo"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :model="addForm" label-position="right" :label-width="100">
          <FormItem label="">
              <Input v-model="addForm.authorInfo" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.refTitle" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Pubmed ID">
              <Input v-model="addForm.pubmedID" placeholder="Pubmed ID" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.magazine" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Issue">
              <Input v-model="addForm.issue" placeholder="Issue" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="Volume">
              <Input v-model="addForm.volume" placeholder="Volume" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.publicYear" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.pageNum" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')"></Button>
            <Button @click="handleSubmit('addForm')"></Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          authorInfo:'',
          refTitle:'',
          pubmedID:'',
          magazine:'',
          issue:'',
          volume:'',
          publicYear:'',
          pageNum:''
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$router.push('/basicdataModal/reference')
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


