<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/basicdatamodal/gene"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="100">
          <FormItem label="Gene Symbol" prop="Symbol">
              <Input v-model="addForm.Symbol" autofocus placeholder="Gene Symbol" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="GeneName">
              <Input v-model="addForm.GeneName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="ChrName">
            <Input v-model="addForm.ChrName" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
              <Input v-model="addForm.Location" placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="">
            <Input v-model="addForm.Synonyms" type="textarea" :rows="4" placeholder="" style="width: 280px"></Input>
            <Button type="dashed" shape="circle" style="width:120px"></Button>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')"></Button>
            <Button @click="backList"></Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
import { sendChrID, reciveChrID } from '../../../assets/switchChrName2ID.js'
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          Symbol:'',
          GeneName:'',
          ChrName:'',
          Location:'',
          Synonyms:''
        },
        rule:{
          Symbol:[
            {
              required: true, message: 'Gene Symbol', trigger: 'blur,change'
            }
          ],
          GeneName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ],
          ChrName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            let obj = this.addForm;
            obj.ChrID = sendChrID(obj.ChrName)
            if(obj.ChrID!='error'){
              this.$http.post("/php/gene/add.php",qs.stringify(obj)).then(response=>{
                console.log(response.data);
                if(response.data==='wrong'){
                  this.$Message.error('');
                  return false;
                }
                if(response.data){
                  this.$Message.success('...');
                  setTimeout(()=>{
                  this.$router.push('/basicdatamodal/gene');      
                  },1000)
                }else{
                  this.$Message.error('');
                }
              })
            }else{
              this.$Message.error('');
            }
          }
        })
      },
      backList(){
        this.$router.push("/basicdatamodal/gene")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


