<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem to="/targetedtherapymodal/variantprofile"></BreadcrumbItem>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="" prop="MolProfileName">
            <Input v-model="addForm.MolProfileName" autofocus placeholder="VariantProfile" style="width: 280px"></Input>
          </FormItem>
          <!--<FormItem label="">
            <Input v-model="addForm.varProDesc" type="textarea" :rows="4" style="width: 280px"></Input>
          </FormItem>-->
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')"></Button>
            <Button @click="backList"></Button>
          </FormItem>
        </Form>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require("qs")
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        addForm: {
          MolProfileName:'',
          // varProDesc:'',
        },
        rule:{
          MolProfileName:[
            {
              required: true, message: '', trigger: 'blur,change'
            }
          ]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(this.addForm.MolProfileName.trim()===''){
              this.$Message.warning('');
              return false;
            }
            let obj={};
            obj.MolProfileName = this.addForm.MolProfileName.trim()
            this.$http.post('/php/targetedtherapymodal/addnewVariantProfile.php',qs.stringify(obj)).then(response=>{
              // console.log(response.data);
              if(response.data==='wrong'){
                this.$Message.warning('');
                return false;
              }
              if(response.data){
                this.$Message.success('...');
                setTimeout(()=>{
                  this.$router.push("/targetedtherapymodal/variantprofile")
                },1000)
              }else{
                this.$Message.error('');
              }
            })
          }
        })
      },
      backList(){
        this.$router.push("/targetedtherapymodal/variantprofile")
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


