<template>
  <div class="container">
    <Breadcrumb>
      <BreadcrumbItem></BreadcrumbItem>
    </Breadcrumb>
    <Content class="mainContent">
      <div class="batchAddContainer">
        <p></p>
        <Button type="primary"></Button>
        <Button></Button>
      </div>
      <div class="singleAddContainer">
        <p></p>
        <Form ref="addForm" :model="addForm" :rules="rule" label-position="right" :label-width="120">
          <FormItem label="" prop="TranscriptName">
            <Input v-model="addForm.TranscriptName" autofocus placeholder="" style="width: 280px"></Input>
          </FormItem>
          <FormItem label="" prop="IsCurrentUsedTranscript">
            <RadioGroup v-model="addForm.IsCurrentUsedTranscript">
              <Radio label="yes">yes</Radio>
              <Radio label="no">no</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem :label-width="10">
            <Button type="primary" @click="handleSubmit('addForm')"></Button>
            <Button @click="backList"></Button>
          </FormItem>
        </Form>
        <Modal v-model="modal" title="" @on-ok="">
          <h2></h2>
        </Modal>
      </div>
    </Content>
    
  </div>
</template>
<script>
var qs = require('qs')
  export default {
    mounted() {
      let contentHeight=window.innerHeight - 80-75 + 'px'
      $(".ivu-layout-content").css("minHeight",contentHeight)
      let asideHeight = $(".container").innerHeight + 'px'
      $('.ivu-layout-sider').css('height', asideHeight)
    },
    data() {
      return {
        geneID:location.search.split("=")[1],
        addForm: {
          GeneID:location.search.split("=")[1],
          TranscriptName:'',
          IsCurrentUsedTranscript:'no'
        },
        rule: {
          TranscriptName: [
            { required: true, message: '', trigger: 'blur,change' }
          ]
        },
        modal: false
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$http.post("/php/gene/transcript/add.php",qs.stringify(this.addForm)).then(response=>{
              console.log(response.data);
              if(response.data==='statuswrong'){
                this.modal = true;
                return false;
              }
              if(response.data){
                this.$Message.success('...');
                  setTimeout(()=>{
                    this.$router.push("/basicdatamodal/transcriptlist?geneid=" + this.geneID)
                  },1000)
                }else{
                  this.$Message.error('');
                }
            })
          }
        })
        console.log(this.addForm);
        // this.$router.push('/basicdatamodal/chromo')
      },
      backList(){
        this.$router.push("/basicdatamodal/transcriptlist?geneid=" + this.geneID)
      }
    }
  }
</script>
<style lang="less" scoped>
@import "../../../assets/themeColor.less";
.container{
  position: relative;

  .ivu-breadcrumb {
    margin: 10px 0;
  }
  .ivu-layout-content {
    padding: 10px;
    background-color: #fff;
  }
  .batchAddContainer,
  .singleAddContainer{
    padding:5px 10px;
    margin-bottom:15px;
    border:@bgc 1px solid;
    p{
      height:30px;
      line-height:30px;
      color:@themeColor;
      font-size:14px;
      margin-bottom:10px;
    }
    .ivu-btn{
      width:90px;
      margin:0 20px 5px;
      padding:10px 15px;
    }
  }
  .singleAddContainer{
    border-color:transparent;
  }
}
</style>


